export default {
	data() {
		return {
			lib: []
		}
	},
	methods: {
		deleATest(index) {
			this.lib.splice(index, 1)
			uni.setStorage({
			    key: 'test',
			    data: this.lib,
			    success: function () {
			        console.log('success');
			    }
			});
		}
	},
	onShow() {
		let self = this
		uni.getStorage({
			key: 'test',
			success: function (res) {
				if(res.data instanceof Array && res.data.length > 0) {
					self.lib = res.data
				}
			}
		});
	}
}
